@import '@/sass/index.scss';










































footer {
	background-color: $bkg_gray;
	height: 300px;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
	padding: $space $main_margin;

	.logos {
		height: 80%;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center;

		.social-media {
			width: 70%;
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}

	.lists {
		width: 40%;
		display: flex;
		justify-content: space-between;
	}

	ul {
		list-style: none;
		text-align: left;
	}
}
