@import '@/sass/index.scss';


















































.product {
	height: 425px;
	min-width: 242px;
	margin-right: 2%;

	// &:first-of-type {
	// 	margin-left: $main_margin;
	// }

	// &:last-child {
	// 	padding-right: $main_margin;
	// 	box-sizing: content-box;
	// }

	.product-img {
		background-color: $bkg_gray;
		height: 70%;
		cursor: pointer;
		display: flex;
		justify-content: center;
		align-items: flex-end;

		img {
			max-width: 90%;
			max-height: 100%;
		}
	}
}

.info {
	height: 30%;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: flex-start;
	padding: 5%;

	.top-line {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 100%;

		p {
			font-size: 20px;
			cursor: pointer;
		}
	}
}

.details {
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: flex-start;
	height: 80%;
}

.colors, .cart {
	cursor: pointer;
}

