@import '@/sass/index.scss';












































































































.shopping-cart {
	position: relative;

	.icon {
		cursor: pointer;
	}

	.circle {
	background-color: $black;
	color: white;
	margin: 0;
	border-radius: 50%;
	width: 22px;
	height: 22px;
	text-align: center;
	position: absolute;
	top: 1%;
	right: 25%;
	}
}

.bag-view {	
	position: absolute;
	top: 72px;
	right: -15%;
	width: 390px;
	height: 70vh;
	border: none;
	background-color: white;
	list-style: none;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: stretch;
	box-sizing: border-box;
	text-align: left;
	cursor: default;
	padding: $space/4;
	box-shadow: 0px 5px 5px rgba(99, 99, 99, 0.247);
}

.content-view {
	flex: 1;
	overflow: scroll;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	padding: 0 $space/2;

	ul {
		list-style: none;
	}
}

.item-container {
	display: grid;
	flex-direction: column;
	grid-template-columns: 20% 50% 26%;
	grid-template: 1fr;
	column-gap: 2%;

	.h2 {
		font-size: 12px;
	}
}

.product-image {
	grid-column: 1;
	grid-row: 1 / span 2;
	justify-self: center;
	align-self: center;
	
}

.thumbnail {
	max-height: 80px;
}

.product-info {
	grid-column: 2;
	justify-self: left;
	align-self: start;
}

.product-counter {
	grid-column: 2;
	grid-row: 2;
	justify-self: left;
	align-self: center;
}

.product-price {
	grid-column: 3;
	justify-self: right;
	align-self: start;
}

.cart-bin {
	justify-content: right;
	grid-column: 3;
	justify-self: right;
	align-self: center;
}

.total {
	display: flex;
	justify-content: space-between;
}

button {
	margin: 10% auto;;
}

