@import '@/sass/index.scss';









































































.main-modal {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: flex;
	align-items: flex-start;
	justify-content: center;
	background-color: rgba(0, 0, 0, 0.5);
	backdrop-filter: blur(3px);
	padding: 10% 15%;
	z-index: 2;
}

.cross {
	position: fixed;
	right: 15%;
	z-index: 3;
}

.modal-content {
	width: 100%;
	height: 100%;
	background-color: $bkg_gray;
	display: flex;
	justify-content: center;
	align-content: center;
}

.inner-content {
	width: 100%;
	display: flex;
	justify-content: center;
	align-content: center;
}
