@import '@/sass/index.scss';
































































































































nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
	background-color: $bkg_gray;
	padding: 5px $main_margin;
}

.social-media {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 136px;
	margin-left: 20px;

	img {
		cursor: pointer;
	}
}

.options {
	display: flex;
	justify-content: space-between;
	align-items: center;
	width: 45%;

	.option {
		display: flex;
		justify-content: space-evenly;
		align-items: center;
	}

	.menu {
		display: flex;
		justify-content: space-between;
		align-items: center;
		width: 60%;

		a {
			color: $black;
			text-decoration: none;

			&.router-link-exact-active {
			text-decoration: underline;
			}
		}
	}

	.log-in {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		width: 130px;
		cursor: pointer;
		position: relative;
		
		p {
			margin-left: $space/4;
		}
	}

	.account {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.account-options {
		position: absolute;
		top: $top_nav_height;
		right: $main_margin;
		min-width: 130px;
		height: fit-content;
		background-color: white;
		border: none;
		box-shadow: 0px 5px 5px rgba(99, 99, 99, 0.247);
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: flex-end;
		z-index: 2;

		ul {
			list-style: none;
			padding: 0 $space/4 $space/4;
			text-align: right;
			
			a {
				text-decoration: none;
				color: $black;
			}

			li {
				margin: 20px 0;
				cursor: pointer;
			}
		}
	}
}
