@import '@/sass/index.scss';






































































































.ad-grid {
	margin: $space $main_margin;
	display: grid;
	grid-template-columns: repeat(4, minmax(0, 1fr));
	grid-template-rows: repeat(2, 1fr);
	gap: 1%;
}

.single-img {
	grid-column: 1 / span 2;
	grid-row: 1 / span 2;
	align-self: center;

	img {
		object-fit: cover;
		width: 100%;
		max-height: 100%;
	}
}

.text {
	grid-column: 3;
	grid-row: 1;
	align-self: center;

	img {
		object-fit: cover;
		width: 100%;
		max-height: 100%;
	}	
}

.hold {
	grid-column: 4;
	grid-row: 1;
	align-self: center;

	img {
		object-fit: cover;
		width: 100%;
		max-height: 100%;
	}
}

.listen {
	grid-column: 3;
	grid-row: 2;

	img {
		object-fit: cover;
		width: 100%;
		max-height: 100%;
	}
}

.discover {
	grid-column: 4;
	grid-row: 2;
	align-self: center;
	cursor: pointer;
	
	img {
		object-fit: cover;
		width: 100%;
		max-height: 100%;
	}
}

.products-view {
	display: flex;
	justify-content: space-between;
	align-items: center;
	overflow: scroll;

	.product:first-of-type {
		margin-left: $main_margin;
	}

	.product:last-child {
		padding-right: $main_margin;
		box-sizing: content-box;
	}
}

.ad-window {
	margin: $space $main_margin;
	position: relative;

	img {
		width: 100%;
		height: auto;
	}

	.btn-medium {
		position: absolute;
		top: 75%;
		right: 0%;
	}

	span {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	p {
		color: white;
	}

	.icon {
		height: 20px;
		width: 24px;
	}
}
